import React from "react"
import styled from "styled-components"
import Video from '../assets/videos/video-1.mp4'
import {Button} from './Button'
const Hero = () => {
  return (
    <>
    <HeroContainer>

        <HeroBg>
            <VideoBg src={Video} type="video/mp4" playsInline autoPlay loop muted  />
        </HeroBg>

        <HeroContent>
            <HeroItems>
                <HeroH1>Unreal Destinations</HeroH1>
                <HeroP>Out of this world</HeroP>
                <Button primary="true" big="true" round="true" to="/trips">Tavel Now</Button>
            </HeroItems>    
        </HeroContent>

    </HeroContainer>
      
    </>
  )
}

export default Hero

const HeroContainer = styled.div`
    background: #0c0c0c;
    display: flex;
    justify-content: center;
    align-items:center;
    height:100vh;
    padding:0 1rem;
    color:#fff;
    margin-top:-80px;

    :before{
        content: "";   
        position: absolute;
        top: 0;bottom: 0;right: 0;left: 0;z-index: 2;
        background: linear-gradient(
            180deg,
            rgba(0,0,0,0.2) 100%,
            rgba(0,0,0,0.6) 100%
        ),linear-gradient(
            180deg,
            rgba(0,0,0,0.2) 100%,
            transparent 10%
        );
        }
`

const HeroBg = styled.div`
position: absolute;
top: 0;
bottom: 0;
right: 0;
left: 0;
width: 100%;
height: 100%;
overflow: hidden;
`

const VideoBg = styled.video`
    width:100%;
    height:100%;
    -o-object-fit:cover;
    object-fit:cover;
`

const HeroContent = styled.div`
    z-index:3;
    height:calc(100vh-80px);
    max-height:100%;
    padding:0rem calc((100vw-1300px) / 2)
`

const HeroItems = styled.div`
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100vh;
    max-height: 100%;
    padding:0;
    color:#fff;
    line-height: 1.1;
    font-weight: bold;

`

const HeroH1 = styled.h1`
    font-size:clamp(1.5rem,6vw,4rem)
    margin-bottom: 1.5rem;
    letter-spacing: 3px;
    padding: 0 1rem;
`

const HeroP = styled.p`
font-size: clamp(1rem, 3vw,2rem);
margin-bottom: 2rem;
font-weight:400;
`